// @media screen and (max-width: 1200px)



//IPAD PRO 1024px

@media screen and (max-width: 991px) {


    .devil {
        &_result {
            width: 400px;
            top: -193px; } }
    .popup {
        .modal {
            width: 60%;
            &__result {
                &-img-block {
                    height: 100%; }
                &-img {
                    &_gameOver {
                        padding: 0;
                        padding-top: 10px; } } } } }
    .button_result {
        font-size: 16px; }
    .popup__shine {
        background-repeat: no-repeat;
        background-size: 100%; }
    .game {
        .container {
            width: 750px; } }
    .dialog {
        width: 200px;
        top: -200px;
        right: -50px;
        z-index: 7;
        &_menu {}
        &_victory {
            top: -250px;
            right: -100px; } }

    .shadow {
        height: 60%; }
    .popup {
        &__shine {
            width: 100%;
            height: 100%; } } }



//IPAD
@media (max-width: 768px) {

    .popup {
        .modal {
            width: 70%;
            &__result {
                &-img-block {
                    height: 140px; } } } }
    .footer {
        &__block {
            max-width: 100%;
            padding: 0 25px; } }
    .shadow-block {
        display: none; }
    .main-menu {
        &__logo {
            top: 15%;
            transform: translate(-50%, -50%);
            width: 300px; }
        .modal {
            width: 450px;
            &__text-block {
                font-size: 12px; }
            .button {
                min-width: 31%;
                font-size: 14px;
                &__check-icon {
                    margin-left: 4px; } } }
        &__overlay {
            background-repeat: no-repeat;
            background-size: 100%; } }
    .main-menu {
        .modal {
            top: 60%;
            left: 50%;
            transform: translate(-50%, -50%);
            padding-top: 50px; } }
    .dialog {
        width: 200px;
        &_victory {
            right: -40px; } }
    .bottom-line {
        width: 550px; }
    .progress-bar {
        width: 300px; }
    .devil {
        &__image {
            width: 100%;
            &_menu {
                top: -130px;
                width: 400px; }
            &_up {
                top: 100px; } } }

    .shadow {
        height: 70%; }
    .game {
        height: 950px;
        width: 100%;
        .container {
            width: 100%;
            height: 100%; } }
    .card-list {
        &__block {
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 500px;
                height: 500px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                width: 550px;
                height: 275px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
                width: 550px;
                height: 400px; } } } }



@media only screen and (max-width: 575px) {
    .popup {
        .modal {
            &__frame {
                &_gameOver {
                    padding: 5px; } } } }

    .shadow {
        // width: 1000px
        height: 80%; }
    .bottom-line {
        width: 90%;
        .progress-bar {
            width: 50%; } }
    .dialog {
        &_victory {
            width: 160px; }
        &_gameOver {
            width: 160px;
            right: -40px; } }
    .devil {
        &__image {
            &_up {
                top: 15%; }
            &_menu {
                top: -130px; } }
        &_result {
            &_victory {
                top: -155px;
                width: 320px; }
            &_gameOver {
                width: 320px;
                top: -157px; } } }
    .modal {
        width: 90%;
        padding: 15px;
        &__result {
            &-img-block {
                height: 100% !important; } }
        &__result-img {
            width: 100%; }
        &__buttons-block {
            width: 100%;
            .button {
                min-width: 0;
                font-size: 14px; } } }

    .card-list {
        &__block {
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 400px;
                height: 400px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 400px;
                height: 325px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
                width: 400px;
                height: 400px; } } } }



//IPHONE_X IPHONE_6/7/8 PLUS
@media only screen and (max-width: 480px) {
    .popup {
        .modal {
            width: 90%; }

        &__overlay {
            background: url('../../images/background-min.jpg') center center; } }
    .footer {
        display: none; }

    .dialog {
        &_menu {
            width: 150px;
            z-index: 7;
            top: -150px;
            right: -50px; }
        &_victory {
            width: 130px;
            right: -5px;
            top: -220px; } }

    .modal {
        &__buttons-block {
            flex-direction: column;
            .button {
                font-size: 16px;
                margin-bottom: 10px;
                display: flex;
                align-items: center;
                flex-direction: column;
                &:last-child {
                    margin-bottom: 0; }
                &__check-icon {
                    margin-left: 0;
                    margin-top: 7px; } } } }
    .devil {
        &__image {
            top: 40%;
            transform: translate(-50%, -50%);
            width: 90%;
            opacity: 0;
            &_up {
                top: 20%;
                opacity: 1; }
            &_menu {
                opacity: 1;
                top: -30px;
                transform: translate(-50%, -50%);
                width: 375px; } }
        &_result {
            &_victory {
                top: -165px;
                width: 340px; }
            &_gameOver {
                top: -128px;
                width: 260px; } } }

    .main-menu {
        &__logo {
            width: 55%;
            top: 15%;
            transform: translate(-50%, -50%); }

        .modal {
            width: 90%;
            top: 65%;
            transform: translate(-50%, -50%);
            padding: 15px;
            &__buttons-block {
                flex-direction: row; }
            .button {
                height: 100%;
                padding: 15px;
                width: 100%;
                font-size: 14px;
                margin-right: 8px;
                &:last-child {
                    margin-right: 0; } } }
        &__overlay {
            background-image: none; } }

    .timer {
        line-height: 1.5;
        left: 50%;
        transform: translateX(-50%); }
    .progress-bar {

        width: 60%; }
    .bottom-line {
        width: 90%;
        bottom: 10px;
        &__buttons {
            .button {
                padding: 15px;
                height: 100%;
                font-size: 12px;
                margin-right: 5px;
                &:last-child {
                    margin-right: 0; } } } }
    .shadow {
        display: none; }
    .game {
        height: 100%;
        .container {
            background: $dark-brown; } }
    .card-list {
        padding-top: 45%;
        &__block {
            background: $dark-brown;
            box-shadow: 0 -25px 25px $dark-brown;
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                width: 325px;
                height: 250px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
                width: 325px;
                height: 325px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
                width: 325px;
                height: 400px; } } } }


//IPHONE_5S/SE   GALAXY_S5
