                top: -122px;
                width: 250px; } } }
    .main-menu {
        .modal {
            width: 90%;
            top: 67%;
            transform: translate(-50%, -50%);
            padding: 15px;
            &__buttons-block {
                flex-direction: row; }
            .button {
                height: 100%;
                padding: 14px;
                font-size: 12px; } } }
    .card-list {
        padding-top: 185px;
        &__block {
            grid-gap: 2px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 275px;
                height: 275px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
                width: 275px;
                height: 275px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
                width: 275px;
                height: 275px; } } } }
