.popup {
    position: absolute;
    z-index: 3;
    width: 100%;
    height: 100%;
    &__black {
      width: 150%;
      height: 150%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: rgba(0,0,0,1); }
    &__shine {
      width: 100%;
      height: 100%;
      position: absolute;
      background: center center / cover;
      background-size: cover;
      background-position: center center;
      background-image: url('../../images/victory_shine-min.png');
      background-repeat: no-repeat;
      animation: shineRotate 15s linear infinite;
      &_red {
        background: url('../../images/game-over_shine-red-min.png') center center no-repeat; } }
    &_victory {}
    &__overlay {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        &_result {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%); } }
    &__modal {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 500px;
        height: 400px;
        background: grey; } }
.main-menu {
  position: relative;
  width: 100%;
  height: 100%;
  background: #22000a;
  .modal {
    padding: 40px;
    padding-top: 60px; }


  &__logo {
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
    width: 400px;
    z-index: 3;
    user-select: none; }
  &__overlay {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: center center / cover;
    background-size: cover;
    background-position: center center;
    background-image: url('../../images/menu_background.jpg');
    animation: backgroundRotate 5s linear infinite; } }
.modal {
    width: 560px;
    left: 50%;
    transform: translateX(-50%);
    background: $dark-brown-gradient;
    border-radius: 10px;
    box-shadow: 0px 10px 20px rgba(0,0,0,0.7), inset 0px 0px 10px rgba(255,66,0,0.5);
    border: 2px solid black;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 15px;
    top: 480px;
    z-index: 6;
    &__buttons-block {
      display: flex;
      justify-content: space-between; }
    &__text-block {
      padding: 20px;
      border-radius: 8px;
      background: #2e0209;
      color: $pale-orange;
      font-size: 16px;
      font-family: 'Cuphead Felix';
      margin-bottom: 20px;
      text-align: center;
      line-height: 1.5;
      box-shadow: inset 0px 3px 3px rgba(0,0,0,0.5), 0px 0px 10px rgba(255,66,0,0.5);
      &_result {
        height: 200px; } }
    &__result-img {
      width: 100%;
      padding-bottom: 20px;

      &-block {
        height: 200px; }
      &_gameOver {
        padding-top: 30px; } }
    &__frame {
      border: 1px solid #ffb652;
      border-radius: 5px;
      padding: 25px;
      box-shadow: 0 0 5px rgba(255,182,82,0.6);
      &_gameOver {
        border: none;
        box-shadow: none; } }
    &_result {
      top: 60%;
      left: 50%;
      transform: translate(-50%, -50%); } }

@keyframes shineRotate {
  0% {}

  100% {
    transform: rotate(360deg); } }
@keyframes backgroundRotate {
  0% {}

  100% {
    transform: rotate(360deg); } }




.popup__modal-enter {
  opacity: 0; }

.popup__modal-enter-active {
  opacity: 1;
  transition: opacity 300ms, transform 300ms; }


.popup__modal-exit {
  opacity: 1;
  transition: opacity 300ms, transform 300ms; }


.popup__modal-exit-active {
  opacity: 0;
  transition: opacity 300ms, transform 300ms; }




.popup__modal_result-enter {
  opacity: 0;
  transform: scale(0.5); }

.popup__modal_result-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms; }


.popup__modal_result-exit {
  opacity: 1;
  transition: opacity 300ms, transform 300ms; }


.popup__modal_result-exit-active {
  opacity: 0;
  transition: opacity 300ms, transform 300ms;
  transform: scale(0.5); }



.dialog {
  position: absolute;
  z-index: 3;
  width: 250px;
  right: 70px;
  top: 260px;
  // transition: all 0.5s ease
  animation: pulse 1s ease infinite;
  &_menu {
    top: -230px;
    right: -130px; }
  &_victory {
    top: -250px;
    right: -120px; }
  &_gameOver {
    top: -250px;
    right: -120px; } }

@keyframes pulse {
  0% {
    transform: scale(1); }

  50% {
    transform: scale(0.9); }

  100% {
    transform: scale(1); }

